<template>
  <li class="dropdown user user-menu">
    <a href="javascript:;" class="dropdown-toggle" data-toggle="dropdown" @click="logoutAction">
      <!-- The user image in the navbar-->
      
     <img src="/static/img/images/dculogo2.png" class="user-image" />
      <!--    <img :src="user.avatar" class="user-image" alt="User Image"> -->
      <!-- hidden-xs hides the username on small devices so only the image appears. -->
      <span class="hidden-xs">Logout</span>
    </a>
    <!-- Account Info and Menu -->
    <ul class="dropdown-menu">
      <li class="user-header" style="height:auto;min-height:85px;padding-bottom:15px;">
        <p>
        <!--   <img src="/static/img/images/dculogo2.png" class="image" style="width:50px;"/> -->
        
          <small>--Admin--</small>
           <small>DCU</small>
        </p>
      </li>
      <li class="user-footer" @click="logoutAction">
        <a href="javascript:;" class="btn btn-default btn-flat btn-block">
          <i class="fa fa-sign-out"></i>
          <span>Logout</span>
        </a>
      </li>
    </ul>
  </li>
</template>
<script>
import VueElementLoading from 'vue-element-loading'
import CripNotice from 'crip-vue-notice'
export default {
  name: 'UserMenu',
  props: ['user'],
  components: {
    CripNotice,
    VueElementLoading
  },
  data() {
    return {
      section: 'Login',
      loading: '',
      username: '',
      password: '',
      response: '',
      userrole: ''
    }
  },
  methods: {
    logoutAction() {
      this.$router.replace({ name: 'Login' })
    }}

}
</script>



