<template>
  <section class="content" id="uploadFirmwarepane">
    <vue-element-loading :active="isLoading" spinner="spinner" color="#000" :is-full-screen="true"/>
    <div class="row">
      <div class="col-lg-12">
       <ul class="timeline">
        <li>
          <i class="fa fa-download bg-yellow"></i>
          <div class="timeline-item">
            <span class="time">&nbsp;</span>
            <h3 class="timeline-header">Download Configuration</h3>
            <div class="timeline-body">
              <div class="row">
              <div class="col-lg-12">
                <button type="submit" class="btn btn-default btn-md">Download</button>
              </div>
              </div>
            </div>
          <!--  <div class="timeline-footer">
              <a>&nbsp;</a>
            </div> -->
          </div>
        </li>
        <li>
          <i class="fa fa-upload bg-blue"></i>
          <div class="timeline-item">
            <span class="time">&nbsp;</span>
            <h3 class="timeline-header">Update Configuration</h3>
            <div class="timeline-body">
                <div class="row">
              <div class="col-lg-12">
                     <input type="file" id="file1" ref="file1" v-on:change="handleFile1Upload()" />
                     <p></p>
                      <button id="upload_BTN" type="button" class="btn btn-sm btn-default" @click="submitFile1()">
                           Upload and Apply
                        </button>
              </div>
              </div>
            </div>
          </div>
        </li>
        <!-- timeline 3333333333333333 -->
          <li>
          <i class="fa fa-upload bg-red"></i>
          <div class="timeline-item">
            <span class="time">&nbsp;</span>
            <h3 class="timeline-header">Update Firmware</h3>
            <div class="timeline-body">
              <div class="row">
                 <div class="col-lg-12">
                       <input type="file" id="file2" ref="file2" v-on:change="handleFile2Upload()" />
                     <p></p>
                      <button id="upload_BTN" type="button" class="btn btn-sm btn-default" @click="submitFile2()">
                            Upload and Apply
                      </button>
              </div>
              </div>
            </div>
          </div>
        </li>
        </ul>
      </div>
    </div>
  </section>
</template>

<script>
import CripNotice from 'crip-vue-notice'
import VueElementLoading from 'vue-element-loading'
export default {
  name: 'UploadFirmware',
  components: {
    CripNotice,
    VueElementLoading
  },
  data () {
    return {
      isLoading: false,
      file1: '',
      file2: ''
    }
  },
  created(){
    if (this.$session.get("loginsession") == "") {
       this.$router.replace({ path: '/' });
       return false;
    }
  },
  mounted () {
    this.isLoading = true;
    let innerMinHeight = window.innerHeight - 122 + 'px';
    document.getElementById('uploadFirmwarepane').style.minHeight = innerMinHeight;
     setTimeout(() => this.isLoading = false, 500);
  },
  methods: {
    handleFile1Upload () {
      this.file1 = this.$refs.file1.files[0]
    },
    submitFile1() {
      if (this.file1 === undefined || this.file1 === '') {
        this.warningMessage('Please upload the config file and proceed further.')
        return false
      }
      this.isLoading = true
      var read = new FileReader()
      read.readAsArrayBuffer(this.file1)
      read.onloadend = function() {
        var xmlhttp = new XMLHttpRequest()
        var urlData = window.location.protocol + '//' + window.location.host + '/' + 'cgi-bin/uploadconfig.cgi'
        xmlhttp.onreadystatechange = function() {
          if (xmlhttp.readyState === 4 && xmlhttp.status === 200) {
            try {
              var DataObj = JSON.parse(xmlhttp.responseText)
              if (DataObj.status === 'success') {} else {
              }
            } catch (error) {
              this.isLoading = false
              alert(error)
            }
          }
        }
        xmlhttp.open('POST', urlData, false)
        // xmlhttp.setRequestHeader("Content-type", "multipart/form-data");
        // xmlhttp.setRequestHeader("charset", "iso-8859-1");
        xmlhttp.setRequestHeader('Cache-Control', 'no-cache')
        xmlhttp.send(read.result)
        // this.isLoading= false;
        // xmlhttp.sendAsBinary(read.result);
      }
    },
    handleFile2Upload () {
      this.file2 = this.$refs.file2.files[0]
    },
    submitFile2() {
      if (this.file2 === undefined || this.file2 === '') {
        this.warningMessage('Please upload the firmware and proceed further.')
        return false
      }
      this.isLoading = true
      var read = new FileReader()
      read.readAsArrayBuffer(this.file2)
      read.onloadend = function() {
        var xmlhttp = new XMLHttpRequest()
        var urlData = window.location.protocol + '//' + window.location.host + '/' + 'cgi-bin/uploadconfig.cgi'
        xmlhttp.onreadystatechange = function() {
          if (xmlhttp.readyState === 4 && xmlhttp.status === 200) {
            try {
              var DataObj = JSON.parse(xmlhttp.responseText)
              if (DataObj.status === 'success') {} else {
              }
            } catch (error) {
              this.isLoading = false
              alert(error)
            }
          }
        }
        xmlhttp.open('POST', urlData, false)
        // xmlhttp.setRequestHeader("Content-type", "multipart/form-data");
        // xmlhttp.setRequestHeader("charset", "iso-8859-1");
        xmlhttp.setRequestHeader('Cache-Control', 'no-cache')
        xmlhttp.send(read.result)
        // this.isLoading= false;
        // xmlhttp.sendAsBinary(read.result);
      }
    },
    isNumber: function(evt) {
      evt = (evt) ? true : window.event
      var charCode = (evt.which) ? evt.which : evt.keyCode
      if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
        evt.preventDefault()
      } else {
        return true
      }
    },
    successMessage(msg) {
      this.$notice.success({
        title: 'Notification',
        description: msg,
        duration: 3,
        onClose() {
          // tslint:disable-next-line:no-console
        }
      })
    },
    warningMessage(msg) {
      this.$notice.warning({
        title: 'Notification',
        description: msg,
        duration: 3,
        onClose() {
          // tslint:disable-next-line:no-console
        }
      })
    },
    infoMessage(msg) {
      this.$notice.info({
        title: 'Notification',
        description: msg,
        duration: 3,
        onClose() {
          // tslint:disable-next-line:no-console
        }
      })
    },
    errorMessage(msg) {
      this.$notice.error({
        title: 'Notification',
        description: msg,
        duration: 3,
        onClose() {
          // tslint:disable-next-line:no-console
        }
      })
    }
  }
}
</script>
<style>
.info-box {
  cursor: pointer;
}
.info-box-content {
  text-align: center;
  vertical-align: middle;
  display: inherit;
}
.fullCanvas {
  width: 100%;
}
.inputTEXTLBLcss{
 font-size: 15px;
}
.inputTEXTcss{
  border: 1px solid lightgray;
  width: 100%;
  height: 30px;
}
.timeline > li > .timeline-item > .timeline-header {
    margin: 0;
    color: #0073b7 !important;
    border-bottom: 1px solid#f4f4f4;
    padding: 10px;
    font-size: 16px;
    line-height: 1.1;
}
</style>



