<template>
  <section class="content" id="uploadFirmwarepane">
    <vue-element-loading :active="isLoading" spinner="spinner" color="#000" :is-full-screen="true"/>
    <div class="row">
      <div class="col-lg-12">
       <ul class="timeline">
        <li>
          <i class="fa fa-yelp bg-yellow"></i>
          <div class="timeline-item">
            <span class="time">&nbsp;</span>
            <h3 class="timeline-header">	&nbsp;</h3>
            <div class="timeline-body">
              <div class="row" v-show="false">
              <div class="col-lg-3">
                <span class="inputTEXTLBLcss">Serial No : </span>
                <input class="inputTEXTcss" id="serialNUMval" disabled/>
                 <p></p>
                <button type="submit" id="serviceHistoryBTN" class="btn btn-default btn-md" @click="viewServiceMethod"><i class="fa fa-eye"></i> View Service history</button>
                <button type="submit" id="addAdminBTN" class="btn btn-primary btn-md" @click="openAdminpopupMethod" v-show="false"><i class="fa fa-user"></i> Admin</button>
              </div>
              <div class="col-lg-4">
                <span class="inputTEXTLBLcss">Firmware Version : </span>
                 <input class="inputTEXTcss" id="serialversionval" disabled/>
              </div>
              </div>
              <div class="row">
              <div class="col-lg-3">
                <button type="submit" id="serviceHistoryBTN" class="btn btn-default btn-md" @click="viewServiceMethod"><i class="fa fa-eye"></i> View Service history</button>
              </div>

              </div>
            </div>
          </div>
        </li>
        </ul>
        <modal name="adminLoginpopup" transition="nice-modal-fade" classes="demo-modal-class1" :min-width="200" :min-height="200" :pivot-y="0.5" :adaptive="true" :draggable="true"
        :scrollable="true"
        :reset="true"
        width="30%"
        height="auto"
        @before-open="beforeOpen"
        @opened="opened"
        @closed="closed"
        @before-close="beforeClose" :clickToClose="false">
        <div class="size-modal-content">
          <div class="row">
            <div class="modal-header">
              <div class="col-lg-6" style="margin-top:-20px;">
              <h4 class="heading" style="color:#5e72e4"> Service Login</h4>
              </div>
              <div class="col-lg-6 text-right" style="margin-top:-20px;">
              <button id="save_closeBTN" type="button" class="btn btn-default" @click="adminLoginpopupclose" style="background:transparent; border:1px solid transparent">
              <span aria-hidden="true" class="white-text">&times;</span>
              </button>
              </div>
               </div>
              </div>
              <div class="modal-body" style="margin-top:-8px;">
              <p>&nbsp;</p>
              <template>
              <form style="padding-left: 0px !important;padding-right: 0px !important;">
              <div class="row">
              <div class="col-lg-12">
              <span>User Name : </span> <input id="adminusernameTXTid" type="text" placeholder="Enter user name" class="inputTEXTcss" v-model="adminusernameTXT"/>
              </div>
              <div class="col-lg-12">
              </div>
              <div class="col-lg-12">
              </div>
              <div class="col-lg-12">
              </div>
              <div class="col-lg-12">
              </div>
              <div class="col-lg-12">
              </div>
              <div class="col-lg-12">
              </div>
              <div class="col-lg-12">
              <span>Password : </span> <input id="adminpasswordTXTid" type="password" placeholder="Enter password" class="inputTEXTcss" v-model="adminpasswordTXT"/>
              </div>
             <div class="col-lg-12">
              </div><div class="col-lg-12">
              </div><div class="col-lg-12">
              </div><div class="col-lg-12">
              </div><div class="col-lg-12">
              </div>
              <div class="col-lg-12">
              </div><div class="col-lg-12">
              </div><div class="col-lg-12">
              </div><div class="col-lg-12">
              </div>
              <div class="col-lg-12">
               <button class="btn btn-md btn-primary" type="submit" @click="adminLoginMethod">Login</button>
              </div>
              </div>
              </form>
              </template>
          </div>
          <div class="modal-footer flex-center">
          </div>
          </div>
          </modal>
        <modal name="addAdmindescriptionPopup" transition="nice-modal-fade" classes="demo-modal-class1" :min-width="200" :min-height="200" :pivot-y="0.5" :adaptive="true" :draggable="true"
        :scrollable="true"
        :reset="true"
        width="55%"
        height="auto"
        @before-open="beforeOpen"
        @opened="opened"
        @closed="closed"
        @before-close="beforeClose" :clickToClose="false">
        <div class="size-modal-content">
          <div class="row">
            <div class="modal-header">
              <div class="col-lg-6" style="margin-top:-20px;">
              <h4 class="heading" style="color:#5e72e4"> Add Service History </h4>
              </div>
              <div class="col-lg-6 text-right" style="margin-top:-20px;">
              <button id="save_closeBTN" type="button" class="btn btn-default" @click="addAdmindescriptionPopupclose" style="background:transparent; border:1px solid transparent">
              <span aria-hidden="true" class="white-text">&times;</span>
              </button>
              </div>
              </div>
              </div>
          <!--Body-->
          <div class="modal-body" style="margin-top:-8px;">
            <div class="row">
            <div class="col-lg-10 text-left"></div>
            <div class="col-lg-2 text-right">
            <button id="adddesrowBtn" class="btn btn-sm btn-primary pull-right" style="margin-top: -5px;margin-right: 0px;" title="Add Description" @click.prevent="adddesrowMethod">
            <img src="/static/img/images/add.png"/> Add Items
            </button>
            </div>
              <div class="col-lg-12">
                 <template>
                <div class="table-responsive" id="adminDescrpPANE" style="margin-top:2px;border:1px solid lightgray" v-bind:style="{ height:admindescripSAVEHeightData+120+'px' }">
                  <table class="table table-bordered table-striped table-responsive-stack" id="adminDescrpPANETableID" style="text-align: center;">
                    <thead class style="position: sticky;top: -1px;z-index:1;">
                      <tr>
                        <th style="width:50px;">S.No</th>
                        <th style="width:50px;">Date</th>
                        <th>Service Description</th>
                        <th style="width:200px;">Person Name</th>
                        <th style="width:50px;">delete</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(adminItems,indx) in adminDesArray" :key="indx">
                        <td style="width:50px;vertical-align: middle;"><span>{{indx+1}}</span></td>
                        <td style="width:50px;vertical-align: middle;">{{adminItems.date}}</td>
                        <td><input :id="'descTXTid'+indx"  type="textarea" class="inputTEXTcss" placeholder="enter service description" :value="adminItems.description" @change="changeDecsTextareaFunction(indx)"/></td>
                        <td style="width:200px;"><input type="text" :id="'personTXTid'+indx" placeholder="enter person name" class="inputTEXTcss" :value="adminItems.person" @change="changepersonTextFunction(indx)"/></td>
                        <td style="width:50px;"> <img :id="'admindescDeleteBTN'+indx"  src="/static/img/images/delete.png" title="Delete Register" @click.prevent="admindescDeleteBTNFunction(indx)"/></td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </template>
              </div>
              </div>
          </div>
          <!--Footer-->
          <div class="modal-footer flex-center">
           <a id="admindescSaveBTN" class="btn btn-primary btnPadding" style="color:#fff" @click="addAdmindescriptionSAVEMethod">Save</a>
           <a id="addAdmindescriptionPopupBNT" type="button" class="btn btn-default waves-effect btnPadding" @click="addAdmindescriptionPopupclose">Cancel</a>
          </div>
          </div>
          </modal>
      </div>
    </div>
  </section>
</template>
<script>
import CripNotice from 'crip-vue-notice'
import VueElementLoading from 'vue-element-loading'
export default {
  name: 'GeneralSetting',
  components: {
    CripNotice,
    VueElementLoading
  },
  data () {
    return {
      isLoading: false,
      username: 'admin',
      password: 'softel',
      adminusernameTXT: '',
      adminpasswordTXT: '',
      admindescripSAVEHeightData: 0,
      adminDesArray: []
    }
  },
  created(){
    if (this.$session.get("loginsession") == "") {
       this.$router.replace({ path: '/' });
       return false;
    }
  },
  mounted () {
    this.isLoading = true;
    let innerMinHeight = window.innerHeight - 122 + 'px'
    document.getElementById('uploadFirmwarepane').style.minHeight = innerMinHeight;
     setTimeout(() => this.isLoading = false, 200);
  },
  methods: {
    beforeOpen () {
    },
    beforeClose () {
    },
    opened (e) {
    },
    closed (e) {
    },
    viewServiceMethod () {
      this.warningMessage('Not available')
      return false
    },
    openAdminpopupMethod () {
      this.$modal.show('adminLoginpopup')
      this.adminusernameTXT = ''
      this.adminpasswordTXT = ''
    },
    adminLoginpopupclose () {
      this.$modal.hide('adminLoginpopup')
    },
    adminLoginMethod () {
      if (this.adminusernameTXT === '') {
        this.warningMessage('Please enter a user name.')
        return false
      }
      if (this.adminpasswordTXT === '') {
        this.warningMessage('Please enter a password.')
        return false
      }
      if (this.adminusernameTXT === this.username && this.adminpasswordTXT === this.password) {
        this.successMessage('Admin login successfully.')
        this.$modal.hide('adminLoginpopup')
        this.admindescripSAVEHeightData = window.innerHeight - 400
        this.$modal.show('addAdmindescriptionPopup')
        this.adminDesArray = []
        this.adminDesArray.push({date: this.getdateformat(new Date()), description: '', person: ''})
        return false
      } else {
        this.errorMessage('Incorrect user name and password.')
        return false
      }
    },
    addAdmindescriptionPopupclose () {
      this.$modal.hide('addAdmindescriptionPopup')
    },
    addAdmindescriptionSAVEMethod () {
      this.$modal.hide('addAdmindescriptionPopup')
      this.successMessage('service history saved successfully.')
      return false
    },
    changeDecsTextareaFunction (rowIndex) {
      this.adminDesArray[rowIndex].description = document.getElementById('descTXTid' + rowIndex).value
    },
    changepersonTextFunction (rowIndex) {
      this.adminDesArray[rowIndex].person = document.getElementById('personTXTid' + rowIndex).value
    },
    adddesrowMethod () {
      this.adminDesArray.push({date: this.getdateformat(new Date()), description: '', person: ''})
    },
    admindescDeleteBTNFunction (rowIndex) {
      this.adminDesArray.splice(rowIndex, 1)
    },
    getdateformat(date) {
      var dtToday = new Date(date)
      var month = dtToday.getMonth() + 1
      var day = dtToday.getDate()
      var year = dtToday.getFullYear()
      var replcema = year
      if (month < 10) {
        month = '0' + month
      }
      if (day < 10) {
        day = '0' + day
      }
      var returndate = replcema.toString().replace('-', '') + '-' + month + '-' + day
      return returndate
    },
    isNumber: function(evt) {
      evt = (evt) ? true : window.event
      var charCode = (evt.which) ? evt.which : evt.keyCode
      if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
        evt.preventDefault()
      } else {
        return true
      }
    },
    successMessage(msg) {
      this.$notice.success({
        title: 'Notification',
        description: msg,
        duration: 3,
        onClose() {
           // tslint:disable-next-line:no-console
        }
      })
    },
    warningMessage(msg) {
      this.$notice.warning({
        title: 'Notification',
        description: msg,
        duration: 3,
        onClose() {
          // tslint:disable-next-line:no-console
        }
      })
    },
    infoMessage(msg) {
      this.$notice.info({
        title: 'Notification',
        description: msg,
        duration: 3,
        onClose() {
          // tslint:disable-next-line:no-console
        }
      })
    },
    errorMessage(msg) {
      this.$notice.error({
        title: 'Notification',
        description: msg,
        duration: 3,
        onClose() {
          // tslint:disable-next-line:no-console
        }
      })
    }
  }
}
</script>
<style>
.info-box {
  cursor: pointer;
}
.info-box-content {
  text-align: center;
  vertical-align: middle;
  display: inherit;
}
.fullCanvas {
  width: 100%;
}
.inputTEXTLBLcss{
 font-size: 15px;
}
.inputTEXTcss{
  border: 1px solid lightgray;
  width: 100%;
  height: 30px;
}
.timeline > li > .timeline-item > .timeline-header {
    margin: 0;
    color: #0073b7 !important;
    border-bottom: 1px solid#f4f4f4;
    padding: 10px;
    font-size: 16px;
    line-height: 1.1;
}
</style>



