<template>
  <section class="content" id="changepassPage">
    <vue-element-loading :active="isLoading" spinner="spinner" color="#000" :is-full-screen="true"/>
    <div class="row">
      <div class="col-lg-12">
       <ul class="timeline">
        <li>
          <i class="fa fa-key bg-yellow"></i>
          <div class="timeline-item">
            <div class="timeline-body">
              <div class="row">
              <div class="col-lg-2">
               <span class="inputTEXTLBLcss">User Name :</span>
              </div>
              <div class="col-lg-4">
                <input v-model="changeusernameTXT" id="changeusernameTXT" placeholder="User Name" class="inputTEXTcss"/> 
              </div>
              </div>
              <p></p>
               <div class="row">
              <div class="col-lg-2">
               <span class="inputTEXTLBLcss">Current Password :</span>
              </div>
              <div class="col-lg-4">
                <input v-model="changecurrpassTXT" id="changecurrpassTXT" placeholder="Current Password" class="inputTEXTcss"/> 
              </div>
              </div>
               <p></p>
               <div class="row">
              <div class="col-lg-2">
               <span class="inputTEXTLBLcss">New Password :</span>
              </div>
              <div class="col-lg-4">
                <input v-model="changenewpassTXT" id="changenewpassTXT" placeholder="New Password" class="inputTEXTcss"/>
              </div>
              </div>
               <p></p>
               <div class="row">
              <div class="col-lg-2">
               <span class="inputTEXTLBLcss">Confirm Password :</span>
              </div>
              <div class="col-lg-4">
                <input v-model="changeconfrimPassTXT" id="changeconfrimPassTXT" placeholder="Confirm Password" class="inputTEXTcss"/>
              </div>
              </div>
               <p></p>
               <div class="row">
              <div class="col-lg-2">
              </div>
              <div class="col-lg-4"> 
                <button id="changePasssubmitBTN" type="button" class="btn btn-sm btn-default">
                            Submit
                      </button>
              </div>
              </div>
            </div>
          <!--  <div class="timeline-footer">
              <a>&nbsp;</a>
            </div> -->
          </div>
        </li>
        </ul>
      </div>
    </div>
  </section>
</template>

<script>
import CripNotice from 'crip-vue-notice'
import VueElementLoading from 'vue-element-loading'
export default {
  name: 'Changepass',
  components: {
    CripNotice,
    VueElementLoading
  },
  data () {
    return {
      isLoading: false,
      changeusernameTXT:'',
      changecurrpassTXT:'',
      changenewpassTXT:'',
      changeconfrimPassTXT:''
    }
  },
  created(){
    if (this.$session.get("loginsession") == "") {
       this.$router.replace({ path: '/' });
       return false;
    }
  },
  mounted () {
    this.isLoading = true;
    let innerMinHeight = window.innerHeight - 122 + 'px'
    document.getElementById('changepassPage').style.minHeight = innerMinHeight;
    setTimeout(() => this.isLoading = false, 200);
  },
  methods: {
    isNumber: function(evt) {
      evt = (evt) ? true : window.event
      var charCode = (evt.which) ? evt.which : evt.keyCode
      if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
        evt.preventDefault()
      } else {
        return true
      }
    },
    successMessage(msg) {
      this.$notice.success({
        title: 'Notification',
        description: msg,
        duration: 3,
        onClose() {
          // tslint:disable-next-line:no-console
        }
      })
    },
    warningMessage(msg) {
      this.$notice.warning({
        title: 'Notification',
        description: msg,
        duration: 3,
        onClose() {
          // tslint:disable-next-line:no-console
        }
      })
    },
    infoMessage(msg) {
      this.$notice.info({
        title: 'Notification',
        description: msg,
        duration: 3,
        onClose() {
          // tslint:disable-next-line:no-console
        }
      })
    },
    errorMessage(msg) {
      this.$notice.error({
        title: 'Notification',
        description: msg,
        duration: 3,
        onClose() {
          // tslint:disable-next-line:no-console
        }
      })
    }
  }
}
</script>
<style>
.info-box {
  cursor: pointer;
}
.info-box-content {
  text-align: center;
  vertical-align: middle;
  display: inherit;
}
.fullCanvas {
  width: 100%;
}
.inputTEXTLBLcss{
 font-size: 15px;
}
.inputTEXTcss{
  border: 1px solid lightgray;
  width: 100%;
  height: 30px;
}
.timeline > li > .timeline-item > .timeline-header {
    margin: 0;
    color: #0073b7 !important;
    border-bottom: 1px solid#f4f4f4;
    padding: 10px;
    font-size: 16px;
    line-height: 1.1;
}
</style>



