<template>
<div>
  <ul class="sidebar-menu">

    <li class="header">MENU </li>
    <router-link tag="li" class="pageLink" to="/dcu/deviceconfig">
      <a @click="screenvalidation">
        <i class="fa fa-cog text-white"></i>
        <span class="page">Device Configuration</span>
      </a>
    </router-link>
     <router-link tag="li" class="pageLink" to="/dcu/upstreamconfig">
      <a @click="screenvalidation">
        <i class="fa fa-cogs text-white"></i>
        <span class="page">Upstream Configuration</span>
      </a>
    </router-link>
      <router-link tag="li" class="pageLink" to="/dcu/paramsconfig">
      <a>
        <i class="fa fa-star-half-o text-white"></i>
        <span class="page">Parameter Configuration</span>
      </a>
    </router-link>
     <router-link tag="li" class="pageLink" to="/dcu/meterconfig">
      <a>
        <i class="fa fa-sliders text-white"></i>
        <span class="page">Meter Configuration</span>
      </a>
    </router-link>
       <router-link tag="li" class="pageLink" to="/dcu/status">
      <a>
        <i class="fa fa-newspaper-o text-white"></i>
        <span class="page">Status</span>
      </a>
    </router-link>
     <router-link tag="li" class="pageLink" to="/dcu/viewdata">
      <a>
        <i class="fa fa-eye text-white"></i>
        <span class="page">View Data</span>
      </a>
    </router-link>
    <router-link tag="li" class="pageLink" to="/dcu/ondemand">
      <a>
        <i class="fa fa-comments-o text-white"></i>
        <span class="page">On Demand</span>
      </a>
    </router-link>
     <router-link tag="li" class="pageLink" to="/dcu/diagnostic">
      <a>
        <i class="fa fa-search-plus text-white"></i>
        <span class="page">Diagnostics</span>
      </a>
    </router-link>
      <router-link tag="li" class="pageLink" to="/dcu/transferfirmware">
      <a>
        <i class="fa fa-exchange text-white"></i>
        <span class="page">Config / Firmware Transfer</span>
      </a>
    </router-link>
  <hr style="margin-top:5px;margin-bottom:5px;"/>
      <router-link tag="li" class="pageLink" to="/dcu/servicehistory">
      <a>
        <i class="fa fa-yelp text-white"></i>
        <span class="page">Service</span>
      </a>
    </router-link>
    <router-link tag="li" class="pageLink" to="/dcu/changepassword">
      <a>
        <i class="fa fa-key text-white"></i>
        <span class="page"> Change Password </span>
      </a>
    </router-link>
     <router-link tag="li" class="pageLink" to="/dcu/saveandapply">
      <a>
        <i class="fa fa-floppy-o text-white"></i>
        <span class="page">Save &amp; Apply</span>
        <small class="label pull-right bg-green"><i class="fa fa-check"></i></small>
      </a>
    </router-link>
     <li id="sidebarresetunitBTN" class="pageLink" @click="restartunitFunction">
      <a><i class="fa fa-refresh text-white"></i>
      <span class="page">Restart Unit</span>
      <small class="label pull-right bg-blue">R</small>
      </a>
     </li>
    <router-link tag="li" class="pageLink" to="/">
      <a>
        <i class="fa fa-sign-out text-white"></i>
        <span class="page"> Logout </span>
      </a>
    </router-link>
  </ul>
   <BlockUI :message="msg" :html="html" v-show="blockdevice">
      <div class="checkbox-wrapper"> <i class="fa fa-cogs fa-spin fa-3x fa-fw" style="color:#1a495e"></i><p></p>
       <p style="color:#1a495e">Please wait while the system saves the configuration and resets, you will be redirected after {{time}} seconds. </p></div>
      </BlockUI>
  </div>
</template>
<script>
import Vue from "vue";
import CripNotice from 'crip-vue-notice';
import VueElementLoading from 'vue-element-loading'
export default {
  name: 'SidebarMenu',
  components: {
    VueElementLoading,
    CripNotice
  },
  data() {
    return {
      sidebarLogsession:this.$session.get("loginsession"),
      sidebarDeviceConfigPageJSON : this.$session.get('get_GenconfigJSON'),
      sidebarUpstreamConfigPageJSON : this.$session.get('get_UpstreamconfigJSON'),
      sidebarMeterConfigPageJSON : this.$session.get('get_DlmsMeterconfigSON'),
      sidebarMasterINSTConfigPageJSON : this.$session.get('get_MasterINSTJSON'),
      sidebarMasterLSConfigPageJSON : this.$session.get('get_MasterLSJSON'),
      sidebarMasterMNConfigPageJSON : this.$session.get('get_MasterMNJSON'),
      sidebarMasterEVENTConfigPageJSON : this.$session.get('get_MasterEVENTJSON'),
      sidebarMasterBILLConfigPageJSON : this.$session.get('get_MasterBILLJSON'),
      sidebarSelINSTConfigPageJSON : this.$session.get('get_SelectedINSTJSON'),
      sidebarSelLSConfigPageJSON : this.$session.get('get_SelectedLSJSON'),
      sidebarSelMNConfigPageJSON : this.$session.get('get_SelectedMNJSON'),
      sidebarSelEVENTConfigPageJSON : this.$session.get('get_SelectedEVENTJSON'),
      sidebarSelBILLConfigPageJSON : this.$session.get('get_SelectedBILLJSON'),
      loading: '',
      blockdevice:false,
      msg: "",
      html: '',
      time: 60,
      isRunning: false,
      interval: null,
    }
  },
  beforeUpdate () {
      console.log('route beforeUpdate');
  },
  destroyed () {
    this.clearALLSession()
  },
  watch:{
    $route (to, from){
      this.screenvalidation();
    }
} ,
  methods: {
    screenvalidation(){
    },
    saveRestartFunction() {
      this.$router.replace({ path: '/dcu/saveandapply' })
    },
    restartunitFunction() {
      this.blockdevice = true;
      this.toggleTimer();
      setTimeout(this.clearALLSession,60000);
    },
    toggleTimer() {
      if (this.isRunning) {
        clearInterval(this.interval);
      } else {
        this.interval = setInterval(this.incrementTime, 1000);
      }
      this.isRunning = !this.isRunning
    },
    incrementTime() {
      this.time = parseInt(this.time) - 1;
    },
    clearALLSession: function() {
     // console.log('----------clearALLSession---------');
      this.blockdevice = false;
      this.$session.clear("loginsession");
      this.$session.clear("UserRole");
      this.$session.clear("get_GenconfigJSON");
      this.$session.clear("get_UpstreamconfigJSON");
      this.$session.clear("get_DlmsMeterconfigSON");
      this.$session.clear("get_MasterINSTJSON");
      this.$session.clear("get_MasterLSJSON");
      this.$session.clear("get_MasterMNJSON");
      this.$session.clear("get_MasterEVENTJSON");
      this.$session.clear("get_MasterBILLJSON");
      this.$session.clear("get_SelectedINSTJSON");
      this.$session.clear("get_SelectedLSJSON");
      this.$session.clear("get_SelectedMNJSON");
      this.$session.clear("get_SelectedEVENTJSON");
      this.$session.clear("get_SelectedBILLJSON");

      this.$session.clear("get_copy_GenconfigJSON");
      this.$session.clear("get_copy_UpstreamconfigJSON");
      this.$session.clear("get_copy_DlmsMeterconfigSON");
      this.$session.clear("get_copy_MasterINSTJSON");
      this.$session.clear("get_copy_MasterLSJSON");
      this.$session.clear("get_copy_MasterMNJSON");
      this.$session.clear("get_copy_MasterEVENTJSON");
      this.$session.clear("get_copy_MasterBILLJSON");
      this.$session.clear("get_copy_SelectedINSTJSON");
      this.$session.clear("get_copy_SelectedLSJSON");
      this.$session.clear("get_copy_SelectedMNJSON");
      this.$session.clear("get_copy_SelectedEVENTJSON");
      this.$session.clear("get_copy_SelectedBILLJSON");
      //this.ws.close();
      this.$router.replace({ path: '/' });
    },
     ValidateIPaddress(inputText) {
       console.log('inputText:', inputText);
      var ipformat = /^(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/;
      if (inputText.match(ipformat)) {
        return true;
      } else {
        this.warningMessage("You have entered invalid IP address!");
        return false;
      }
    },
    successMessage(msg) {
      this.$notice.success({
        title: 'Notification',
        description: msg,
        duration: 3,
        onClose() {
          // tslint:disable-next-line:no-console
        }
      })
    },
    warningMessage(msg) {
      this.$notice.warning({
        title: 'Notification',
        description: msg,
        duration: 3,
        onClose() {
          // tslint:disable-next-line:no-console
        }
      })
    },
    infoMessage(msg) {
      this.$notice.info({
        title: 'Notification',
        description: msg,
        duration: 3,
        onClose() {
          // tslint:disable-next-line:no-console
        }
      })
    },
    errorMessage(msg) {
      this.$notice.error({
        title: 'Notification',
        description: msg,
        duration: 3,
        onClose() {
          // tslint:disable-next-line:no-console
        }
      })
    }
  }
}
</script>
<style>
/* override default */
.sidebar-menu > li > a {
  padding: 12px 15px 12px 15px;
}

.sidebar-menu li.active > a > .fa-angle-left,
.sidebar-menu li.active > a > .pull-right-container > .fa-angle-left {
  animation-name: rotate;
  animation-duration: 0.2s;
  animation-fill-mode: forwards;
}

.treeview-title {
  z-index: 1;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-90deg);
  }
}
</style>



