        this.clicksave = true;
      var checkGEN = _.isEqual(this.$session.get('get_GenconfigJSON'),this.$session.get('get_copy_GenconfigJSON'));
      if(!checkGEN){
          var setGENconfigJSON = this.$session.get('get_GenconfigJSON');
          setGENconfigJSON.CMD_TYPE = 'SET_GEN_CONFIG';
          console.log('SET_GEN_CONFIG');
          this.ws.send(JSON.stringify(setGENconfigJSON));
      }
      var checkUPSTREAM = _.isEqual(this.$session.get('get_UpstreamconfigJSON'),this.$session.get('get_copy_UpstreamconfigJSON'));
