      var checkEVENT = _.isEqual(this.$session.get('get_SelectedEVENTJSON'),this.$session.get('get_copy_SelectedEVENTJSON'));
      if(!checkEVENT){
          var setSelEVENTconfigJSON = this.$session.get('get_SelectedEVENTJSON');
          setSelEVENTconfigJSON.CMD_TYPE = 'SET_DLMS_EVENT_PARAM_CONFIG';
          console.log('SET_DLMS_EVENT_PARAM_CONFIG');
        this.ws.send(JSON.stringify(setSelEVENTconfigJSON));
      }
      var checkBILL = _.isEqual(this.$session.get('get_SelectedBILLJSON'),this.$session.get('get_copy_SelectedBILLJSON'));
      if(!checkBILL){
          var setSelBILLconfigJSON = this.$session.get('get_SelectedBILLJSON');
          setSelBILLconfigJSON.CMD_TYPE = 'SET_DLMS_BILL_PARAM_CONFIG';
          console.log('SET_DLMS_BILL_PARAM_CONFIG');
          this.ws.send(JSON.stringify(setSelBILLconfigJSON));
      }
      }

        this.blockdevice = true;
       this.toggleTimer()
       setTimeout(this.clearALLSession1, this.durationlimit)
      }
    },
    jsonEqual(a,b) {
    return JSON.stringify(a) === JSON.stringify(b);
    },
    toggleTimer () {
      if (this.isRunning) {
        clearInterval(this.interval)
      } else {
        this.interval = setInterval(this.incrementTime, 1000)
      }
      this.isRunning = !this.isRunning
    },
    incrementTime() {
      this.time = parseInt(this.time) - 1
    },
    clearALLSession1() {
      this.blockdevice = false;
      this.$router.replace({ path: '/' })
    },
    isNumber: function(evt) {
      evt = (evt) ? true : window.event
      var charCode = (evt.which) ? evt.which : evt.keyCode
      if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
        evt.preventDefault()
      } else {
        return true
      }
    },
    successMessage(msg) {
      this.$notice.success({
        title: 'Notification',
        description: msg,
        duration: 3,
        onClose() {
          // tslint:disable-next-line:no-console
        }
      })
    },
    warningMessage(msg) {
      this.$notice.warning({
        title: 'Notification',
        description: msg,
        duration: 3,
        onClose() {
          // tslint:disable-next-line:no-console
        }
      })
    },
    infoMessage(msg) {
      this.$notice.info({
        title: 'Notification',
        description: msg,
        duration: 3,
        onClose() {
          // tslint:disable-next-line:no-console
        }
      })
    },
    errorMessage(msg) {
      this.$notice.error({
        title: 'Notification',
        description: msg,
        duration: 3,
        onClose() {
          // tslint:disable-next-line:no-console
        }
      })
    }
  }
}
</script>
