   font-size: 15px;
   color: #1a495e;
   font-weight: bold;
}
.inputTEXTcss{
  border: 1px solid transparent;
  width: 75%;
  height: 30px;
  font-size: 14px;
  font-weight: normal;
}
.timeline > li > .timeline-item > .timeline-header {
    margin: 0;
    color: #0073b7 !important;
    border-bottom: 1px solid#f4f4f4;
    padding: 10px;
    font-size: 16px;
    line-height: 1.1;
}
.margintop8{
  margin-top: -8px;
}
