import DashView from './components/Dash.vue'
import LoginView from './components/Login.vue'
// Import Views - Dash
import DeviceConfigView from './components/views/DeviceConfig.vue'
import UpstreamConfigView from './components/views/UpstreamConfig.vue'
import MeterconfigView from './components/views/Meterconfig.vue'
import ParamConfigView from './components/views/ParamConfig.vue'
import StatusView from './components/views/Status.vue'
import ViewDataView from './components/views/ViewData.vue'
import OnDemandView from './components/views/OnDemand.vue'
import DiagnosticView from './components/views/Diagnostic.vue'
import TransferfirmwareView from './components/views/Transferfirmware.vue'
import ServicehistoryView from './components/views/Servicehistory.vue'
import SaveandApplyView from './components/views/SaveandApply.vue'
import ChangepasswordView from './components/views/Changepassword.vue'

// import DashboardView from './components/views/Dashboard.vue'
// import TablesView from './components/views/Tables.vue'
// import TasksView from './components/views/Tasks.vue'
// import SettingView from './components/views/Setting.vue'
// import AccessView from './components/views/Access.vue'
// import ServerView from './components/views/Server.vue'
// import ReposView from './components/views/Repos.vue'
// import NotFoundView from './components/404.vue'

// Routes
const routes = [{
        path: '/',
        name: 'Login',
        component: LoginView
    },
    {
        path: '/dcu',
        component: DashView,
        children: [{
            path: 'deviceconfig',
            alias: '',
            component: DeviceConfigView,
            name: 'Device',
            meta: { description: 'Configuration' }
        }, {
            path: 'upstreamconfig',
            alias: '',
            component: UpstreamConfigView,
            name: 'Upstream',
            meta: { description: 'Configuration' }
        }, {
            path: 'meterconfig',
            alias: '',
            component: MeterconfigView,
            name: 'Meter',
            meta: { description: 'Configuration' }
        }, {
            path: 'paramsconfig',
            alias: '',
            component: ParamConfigView,
            name: 'Parameter',
            meta: { description: 'Configuration' }
        }, {
            path: 'status',
            alias: '',
            component: StatusView,
            name: 'Status',
            meta: { description: '' }
        }, {
            path: 'viewdata',
            alias: '',
            component: ViewDataView,
            name: 'View Data',
            meta: { description: '' }
        }, {
            path: 'ondemand',
            alias: '',
            component: OnDemandView,
            name: 'On Demand ',
            meta: { description: 'Command' }
        }, {
            path: 'diagnostic',
            alias: '',
            component: DiagnosticView,
            name: 'Diagnostics ',
            meta: { description: '' }
        }, {
            path: 'transferfirmware',
            alias: '',
            component: TransferfirmwareView,
            name: 'Transfer Firmware',
            meta: { description: '' }
        }, {
            path: 'changepassword',
            alias: '',
            component: ChangepasswordView,
            name: 'Change password',
            meta: { description: '' }
        }, {
            path: 'servicehistory',
            alias: '',
            component: ServicehistoryView,
            name: 'Service History',
            meta: { description: '' }
        }, {
            path: 'saveandapply',
            alias: '',
            component: SaveandApplyView,
            name: 'Save and Apply',
            meta: { description: '' }
        }]
    }
]

export default routes;


