<script>
import CripNotice from "crip-vue-notice";
import VueElementLoading from "vue-element-loading";
export default {
  name: "UploadFirmware",
  components: {
    CripNotice,
    VueElementLoading,
  },
  data() {
    return {
      isLoading: false,
      deviceStatustableheight: 0,
      divLength: 6,
      countlength: 0,
      getmeterStatusArray: [],
      getmeterStatuspopupTitle: "",
      getmeterStatuspopupLocation: "",
      getmeterStatuspopupSerialNum: "",
      getmeterStatuspopupCommStatus: "",
      getmeterStatuspopupUpdateTime: "",
      instaLLJSon: "",
      meterstatusInterval:""
    };
  },
  created() {
    if (this.$session.get("loginsession") == "") {
      this.$router.replace({ path: "/" });
      return false;
    }
    this.isLoading = true;
    var RespData = "";
    this.ws = new WebSocket(this.webSocketUrl);
    this.ws.onopen = (e) => {
    this.meterstatusInterval = setInterval(() => {
  // console.log('e', e);
    var commdObj1 = {
        TYPE: "CYCLIC_MESSAGE",
        SEQ_NUM: Math.floor(1000 + Math.random() * 9000).toString(),
        CMD_TYPE: "CYCLIC_INST",
      };
      this.ws.send(JSON.stringify(commdObj1));
      }, 3000);
    };
    this.ws.onmessage = (e) => {
     console.log("status page :start::" + JSON.stringify(e.data));
      try {
        if (e.data.length == 4096){
          RespData = RespData + e.data;
        }
        else {
          RespData = RespData + e.data;
          try {
            var split_char = RespData.split("!");
            var count = split_char.length - 1;
            for (var idx = 0; idx < count; idx++) {
              var websocketBody = JSON.parse(split_char[idx]);
              RespData = "";
              console.log("status page correct data");
              console.log("status page :msg::" + JSON.stringify(websocketBody));
              if (websocketBody.TYPE == "CYCLIC_MESSAGE") {
                this.instaLLJSon = "";
                this.instaLLJSon = websocketBody;
                this.isLoading = false;
              }
            }
          } catch (err) {
            RespData = "";
            console.log("Invalid data");
            console.log(err);
          }
        }
      } catch (err) {
        //  console.log(err)
      }
    };
  },
  mounted() {
    
    let innerMinHeight = window.innerHeight - 140 + "px";
    this.deviceStatustableheight = window.innerHeight - 330 + "px";
    document.getElementById("deviceStatuspane").style.minHeight = innerMinHeight;
    //setTimeout(() => (this.isLoading = false), 500);
    
  },
  destroyed(){
    this.ws.close();
    clearInterval(this.meterstatusInterval);
  },
  methods: {
    beforeOpen() {},
    beforeClose() {},
    meterstatuspopupopened() {
      this.$modal.show("meterstatuspopup");
    },
    meterstatuspopupclosed() {
      this.$modal.hide("meterstatuspopup");
    },
    isNumber: function (evt) {
      evt = evt ? true : window.event;
      var charCode = evt.which ? evt.which : evt.keyCode;
      if (
        charCode > 31 &&
        (charCode < 48 || charCode > 57) &&
        charCode !== 46
      ) {
        evt.preventDefault();
      } else {
        return true;
      }
    },
    getmeterStatus(rowIndex) {
      this.getmeterStatuspopupTitle = "";
      this.getmeterStatuspopupLocation = "";
      this.getmeterStatuspopupSerialNum = "";
      this.getmeterStatuspopupCommStatus = "";
      this.getmeterStatuspopupUpdateTime = "";
      this.getmeterStatusArray = [];
      if (this.instaLLJSon.DATA.INST_PARAM[rowIndex - 1].PARAMS.length != 0) {
        this.getmeterStatuspopupLocation = this.instaLLJSon.DATA.INST_PARAM[
          rowIndex - 1
        ].MET_LOC;
        this.getmeterStatuspopupSerialNum = this.instaLLJSon.DATA.INST_PARAM[
          rowIndex - 1
        ].MET_SERNUM;
        if (this.instaLLJSon.DATA.INST_PARAM[rowIndex - 1].COMM_STATUS == "0") {
          this.getmeterStatuspopupCommStatus = "Not Communicating";
        } else if (
          this.instaLLJSon.DATA.INST_PARAM[rowIndex - 1].COMM_STATUS == "1"
        ) {
          this.getmeterStatuspopupCommStatus = "Communicating";
        } else {
          this.getmeterStatuspopupCommStatus = "Not Configured";
        }
        this.getmeterStatuspopupUpdateTime = this.instaLLJSon.DATA.INST_PARAM[rowIndex - 1].UPDATE_TIME;
        this.getmeterStatusArray = this.instaLLJSon.DATA.INST_PARAM[
          rowIndex - 1
        ].PARAMS;
        var tableCount1 = parseInt(
          this.getmeterStatusArray.length / this.divLength +
            (this.getmeterStatusArray.length % this.divLength == 0 ? 0 : 1)
        );
        this.countlength = tableCount1;
        this.meterstatuspopupopened();
      } else {
        alert("Data Not found");
        return false;
      }
    },
    meterStatuspopupclse() {},
    showGetFormattedDatewithTime(date) {
      var month = date.getMonth() + 1;
      var day = date.getDate();
      var year = date.getFullYear();
      var seconds =
        date.getSeconds() <= 9 ? "0" + date.getSeconds() : date.getSeconds();
      var minutes =
        date.getMinutes() <= 9 ? "0" + date.getMinutes() : date.getMinutes();
      var hour = date.getHours() <= 9 ? "0" + date.getHours() : date.getHours();
      var monthStr = month <= 9 ? "0" + month : month;
      var dateStr = day <= 9 ? "0" + day : day;
      return (
        dateStr +
        "-" +
        monthStr +
        "-" +
        year +
        " " +
        hour +
        ":" +
        minutes +
        ":" +
        seconds
      );
    },
    successMessage(msg) {
      this.$notice.success({
        title: "Notification",
        description: msg,
        duration: 3,
        onClose() {
          // tslint:disable-next-line:no-console
        },
      });
    },
    warningMessage(msg) {
      this.$notice.warning({
        title: "Notification",
        description: msg,
        duration: 3,
        onClose() {
          // tslint:disable-next-line:no-console
        },
      });
    },
    infoMessage(msg) {
      this.$notice.info({
        title: "Notification",
        description: msg,
        duration: 3,
        onClose() {
          // tslint:disable-next-line:no-console
        },
      });
    },
    errorMessage(msg) {
      this.$notice.error({
        title: "Notification",
        description: msg,
        duration: 3,
        onClose() {
          // tslint:disable-next-line:no-console
        },
      });
    },
  },
};
</script>
