<template>
  <section class="content" id="onDemandPage">
    <vue-element-loading :active="isLoading" spinner="spinner" color="#000" :is-full-screen="true"/>
    <div class="row">
      <div class="col-lg-12">
       <ul class="timeline">
        <li>
          <i class="fa fa-comments-o bg-yellow"></i>
          <div class="timeline-item">
            <div class="timeline-body">
              <div class="row">
              <div class="col-lg-2">
               <span class="inputTEXTLBLcss">Meter Serial Number :</span>
              </div>
              <div class="col-lg-3">
                 <select class="inputTEXTcss" name="onDemandSerialNo" id="onDemandSerialNoTXT" v-model="onDemandSerialNoTXT">
															  <option value='Meter Serial Number' selected disabled>Meter Serial Number</option>
															</select>
              </div>
              </div>
              <p></p>
               <div class="row">
              <div class="col-lg-2">
               <span class="inputTEXTLBLcss">Command :</span>
              </div>
              <div class="col-lg-3">
               <select class="inputTEXTcss"  name="onDemandCommand" id="onDemandCommandTXT" v-model="onDemandCommandTXT">
															  <option value='Choose Command' selected disabled>Choose Command</option>
                                <option value='Get Instantaneous Data'>Get Instantaneous Data</option>
                                <option value='Get Load Survey Data (Today)'>Get Load Survey Data (Today)</option>
                                <option value='Get Load Survey Data (For n days)'>Get Load Survey Data (For n days)</option>
                                <option value='Get Billing Data'>Get Billing Data</option>
                                <option value='Get Event Data'>Get Event Data</option>
															</select>
              </div>
              </div>
               <p></p>
               <div class="row">
              <div class="col-lg-2">
              </div>
              <div class="col-lg-4"> 
                <button id="viewdataApplyBTN" type="button" class="btn btn-sm btn-default">
                            Apply
                      </button>
              </div>
              </div>
              <p></p>
              <div class="row" v-show="false">
              <div class="col-lg-2">
                <h5><b>Details :</b></h5>
              </div>
              <div class="col-lg-4"> 
              </div>
              </div>
               <p></p>
              <div class="row" v-show="false">
              <div class="col-lg-12">
                <template>
                    <div class="table-responsive" id="onDemandTablePane" style="border:1px solid lightgray" v-bind:style="{height:onDemandTableheight}">
                      <table class="table table-bordered table-responsive-stack" id="onDemandTableID">
                        <thead style="position: sticky;top: -1px;z-index:1;">
                          <tr>
                            <th>S.No</th>
                            <th>Date</th>
                            <th>View</th>
                            <th>Download</th>
                          </tr>
                        </thead>
                        <tbody v-if="ondemandconfigJson != ''">
                          <tr v-for="(inputItems,index) in ondemandconfigJson.details" v-bind:key="index">
                            <td>{{index+1}}</td>
                            <td>{{inputItems.date}}</td>
                            <td><a class="hyperlink" @click="viewDATA()">View</a></td>
                            <td><a class="hyperlink" @click="downloadDATA()">Download</a></td>
                          </tr>
                        </tbody>
                      </table>
                      <div v-if="ondemandconfigJson == ''" class="text-center text-block">
                        <span>No Records Found</span>
                      </div>
                    </div>
                  </template>
              </div>
              </div>
            </div>
          <!--  <div class="timeline-footer">
              <a>&nbsp;</a>
            </div> -->
          </div>
        </li>
        </ul>
      </div>
    </div>
  </section>
</template>

<script>
import CripNotice from 'crip-vue-notice'
import VueElementLoading from 'vue-element-loading'
export default {
  name: 'Changepass',
  components: {
    CripNotice,
    VueElementLoading
  },
  data () {
    return {
      isLoading: false,
      onDemandSerialNoTXT:'Meter Serial Number',
      onDemandCommandTXT:'Choose Command',
      onDemandTableheight:0,
      ondemandconfigJson:{details:[
      {id:1,date:'13-08-2020',view:'',download:''},
      {id:2,date:'12-08-2020',view:'',download:''},
      {id:3,date:'11-08-2020',view:'',download:''},
      {id:4,date:'10-08-2020',view:'',download:''},
      {id:5,date:'09-08-2020',view:'',download:''},
      {id:6,date:'08-08-2020',view:'',download:''},
      {id:7,date:'07-08-2020',view:'',download:''},
      {id:8,date:'06-08-2020',view:'',download:''},
      {id:9,date:'05-08-2020',view:'',download:''},
      {id:10,date:'04-08-2020',view:'',download:''},
      {id:11,date:'03-08-2020',view:'',download:''},
      {id:12,date:'02-08-2020',view:'',download:''},
      {id:13,date:'01-08-2020',view:'',download:''},
      {id:14,date:'30-07-2020',view:'',download:''}]}
    }
  },
  created(){
    if (this.$session.get("loginsession") == "") {
       this.$router.replace({ path: '/' });
       return false;
    }
  },
  mounted () {
    this.isLoading = true;
    let innerMinHeight = window.innerHeight - 125 + 'px'
    this.onDemandTableheight = window.innerHeight - 350 + 'px'
    document.getElementById('onDemandPage').style.minHeight = innerMinHeight;
    setTimeout(() => this.isLoading = false, 500);
  },
  methods: {
    viewDATA(){
      this.isLoading = true;
      setTimeout(() => this.isLoading = false, 1000);
    },
    downloadDATA(){
      this.isLoading = true;
      setTimeout(() => this.isLoading = false, 1000);
    },
    isNumber: function(evt) {
      evt = (evt) ? true : window.event
      var charCode = (evt.which) ? evt.which : evt.keyCode
      if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
        evt.preventDefault()
      } else {
        return true
      }
    },
    successMessage(msg) {
      this.$notice.success({
        title: 'Notification',
        description: msg,
        duration: 3,
        onClose() {
          // tslint:disable-next-line:no-console
        }
      })
    },
    warningMessage(msg) {
      this.$notice.warning({
        title: 'Notification',
        description: msg,
        duration: 3,
        onClose() {
          // tslint:disable-next-line:no-console
        }
      })
    },
    infoMessage(msg) {
      this.$notice.info({
        title: 'Notification',
        description: msg,
        duration: 3,
        onClose() {
          // tslint:disable-next-line:no-console
        }
      })
    },
    errorMessage(msg) {
      this.$notice.error({
        title: 'Notification',
        description: msg,
        duration: 3,
        onClose() {
          // tslint:disable-next-line:no-console
        }
      })
    }
  }
}
</script>
<style>
.info-box {
  cursor: pointer;
}
.info-box-content {
  text-align: center;
  vertical-align: middle;
  display: inherit;
}
.fullCanvas {
  width: 100%;
}
.inputTEXTLBLcss{
 font-size: 15px;
}
.inputTEXTcss{
  border: 1px solid lightgray;
  width: 100%;
  height: 30px;
}
.timeline > li > .timeline-item > .timeline-header {
    margin: 0;
    color: #0073b7 !important;
    border-bottom: 1px solid#f4f4f4;
    padding: 10px;
    font-size: 16px;
    line-height: 1.1;
}

table {
  font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 100%;
}

table td,
table th {
  border: 1px solid rgb(235, 232, 232);
  padding: 2px;
}

table tbody tr:nth-child(even) {
  background-color: #f2f2f2;
}

table tbody tr:hover {
  background-color: rgb(235, 232, 232);
}
td img {
  cursor: pointer;
}
table th {
  padding-bottom: 1px;
  text-align: center;
  background-color: #1a495e;
  max-height: 40px;
  height: 40px;
  vertical-align: middle !important;
  font-size: 13px;
  color: white;
}
table tr td {
  padding-bottom: 1px;
  text-align: center;
  max-height: 40px;
  height: 40px;
  font-size: 13px;
}
.hyperlink{
cursor: pointer;
text-decoration: underline;
}
.hyperlink:hover{
cursor: pointer;
color: #1a495e;
text-decoration: underline;
}
</style>



